<template>
    <div>
        <basic-header :items="['店铺管理','分类管理']"></basic-header>
        <el-card style="margin-top: 20px">
            <template>
                <el-tabs v-model="activeName">
                    <el-tab-pane label="查看分类" name="first">
                        <el-table
                                :data="tableData"
                        >
                            <el-table-column
                                    prop="id"
                                    label="ID"
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    prop="name"
                                    label="分类名称"
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    prop="thumb"
                                    label="缩略图">
                                <template #default="o">
                                    <img :src="o.row.thumb" alt="" width="100" height="100">
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="sort"
                                    label="产品排序">
                            </el-table-column>
                            <el-table-column
                                    prop="create_time"
                                    label="创建时间">
                            </el-table-column>
                            <el-table-column
                                    prop="update_time"
                                    label="更新时间">
                            </el-table-column>
                            <el-table-column
                                    label="操作">
                                <template #default="o">
                                    <el-button type="text" @click="edit(o.row)">编辑</el-button>
                                    <el-button type="text" @click="del(o.row.id)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-dialog
                                title="编辑分类管理"
                                :visible.sync="dialogVisible"
                                width="50%"
                               >
                            <el-form label-width="100px" ref="updateForm" :rules="rules" :model="updateForm" style="width: 80%;">
                                <el-form-item label="产品名称" prop="name">
                                    <el-input v-model="updateForm.name"></el-input>
                                </el-form-item>
                                <el-form-item label="产品缩略图" prop="thumb">
                                    <el-upload
                                            class="avatar-uploader"
                                            action="/api/upload/upload"
                                            :show-file-list="false"
                                            :on-success="handleAvatarSuccess2"
                                            :before-upload="beforeAvatarUpload">
                                        <img v-if="updateForm.thumb" :src="updateForm.thumb" class="avatar">
                                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
                                </el-form-item>
                                <el-form-item label="产品排序" prop="sort">
                                    <el-input v-model="updateForm.sort"></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="updateform">确 定</el-button>
  </span>
                        </el-dialog>
                        <el-pagination
                                background
                                style="margin-top: 20px"
                                layout="prev, pager, next"
                                :total=total
                                :page-size.sync=pageSize
                                :current-page.sync=page
                        >
                        </el-pagination>
                    </el-tab-pane>
                    <el-tab-pane label="添加分类" name="second">
                        <el-form label-width="100px" ref="submitForm" :rules="rules" :model="form" style="width: 80%;">
                            <el-form-item label="产品名称" prop="name">
                                <el-input v-model="form.name"></el-input>
                            </el-form-item>
                            <el-form-item label="产品缩略图" prop="thumb">
                                <el-upload
                                        class="avatar-uploader"
                                        action="/api/upload/upload"
                                        :show-file-list="false"
                                        :on-success="handleAvatarSuccess"
                                        :before-upload="beforeAvatarUpload">
                                    <img v-if="form.thumb" :src="form.thumb" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </el-form-item>
                            <el-form-item label="产品排序" prop="sort">
                                <el-input v-model="form.sort"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submit">
                                    提交
                                </el-button>
                            </el-form-item>
                        </el-form>
                    </el-tab-pane>
                </el-tabs>
            </template>
        </el-card>
    </div>
</template>

<script>
    import MBXhead from "@/components/MBXhead";

    export default {
        data() {
            return {
                activeName: "first",
                tableData: [],
                page: 1,
                pageSize: 5,
                total: 10,
                api: "/api/Type/type",
                dialogVisible:false,
                form: {
                    name: "",
                    sort: "",
                    thumb: ""
                },
                updateForm:{
                    name: "",
                    sort: "",
                    thumb: ""
                },
                rules: {
                    name: {required: true, message: '请输入产品名称', trigger: 'blur'},
                    sort: {required: true, message: '请输入产品牌排序', trigger: 'blur'},
                    thumb: {required: true, message: '请上传图片'},
                }
            }
        },
        methods: {
            fetchData() {
                this.axios.get(this.api, {
                    params: {page: this.page, pageSize: this.pageSize,}
                }).then(res => {
                    this.tableData = res.data.data;
                    this.total = res.data.total;
                })
            },
            handleAvatarSuccess(res, file) {
                this.form.thumb = res;
                this.$refs.submitForm.validateField("thumb");
            },
            handleAvatarSuccess2(res, file) {
                this.updateForm.thumb = res;
                this.$refs.updateForm.validateField("thumb");
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            submit() {
                this.$refs.submitForm.validate(valid => {
                    if (valid) {
                        this.axios.post(this.api, this.form).then(res => {
                            if (res.data.code === 200) {
                                this.$message.success("提交成功");
                                this.$refs.submitForm.resetFields();
                                this.activeName = "first";
                                this.fetchData();
                            }
                        })
                    } else {
                        this.$message.error("验证失败")
                    }
                })
            },
            del(id) {
                this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.axios.delete(this.api, {
                        params: {id}
                    }).then(res => {
                        if (res.data.code == 200) {
                            this.$message.success("删除成功");
                            this.fetchData()
                        } else {
                            this.$message.error(res.data.msg)
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            edit(o){
                // delete o.create_time;
                this.dialogVisible=true;
                this.updateForm=o;
             },
            updateform(){
                this.$refs.updateForm.validate(valid=>{
                    if(valid){
                        delete this.updateForm.create_time
                        this.axios.put(this.api,this.updateForm).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("修改成功")
                                this.dialogVisible=false;
                                this.fetchData()
                            }else{
                                this.$message.error("修改失败");
                                this.dialogVisible=false;
                            }
                        })
                    }else{
                        this.$message.error("修改失败");
                        this.dialogVisible=false;
                    }
                })
            }
        },
        watch: {
            page: function () {
                this.fetchData()
            }
        },
        components: {
            "basic-header": MBXhead
        },
        created() {
            this.fetchData()
        },
        mounted() {

        },
    }
</script>
<style scoped>
    .avatar-uploader .el-upload {
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
</style>